#root {
  height: 85vh;
  background: rgb(153, 153, 153);
  margin: 2rem;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

h1, p {
  color: rgb(75, 44, 66);
}

input {
  font-size: 20px;
  height: 40px;
  border-radius: 5px;
  border: none;
  text-align: center;
}

button {
  height: 30px;
  width: 150px;
  font-size: 20px;
  background: rgb(218, 173, 27); /*   background: rgb(195, 195, 195); */
  border: none;
  border-radius: 10px;
  cursor: pointer;
  margin: 3px;
}

button:active {
  transform: scale(.95);
}